$text-under-white: #433c3c;

.about-container {
  display: flex;
  justify-content: space-around;
  align-items: flex-end;
}

.about-header {
  color: $text-under-white;
  font-size: 50px;
}

.about-header-content {
  width: 50%;
  height: 500px;
  overflow-y: scroll;
  padding: 0 25px;

  &>div {
    padding: 4rem 1rem;
  }

  &>div>div {
    padding: 4rem 1rem;
  }
}

.about-avatar-wrapper {
  display: flex;
}

.about-avatar {
  width: 430px;
}

.h3-header {
  color: #404eed;
  font-size: 28px;
}

// Mobile Screens
@media only screen and (min-width: 320px) and (max-width: 480px) {
  .about-avatar {
    width: 350px;
  }

  .about-container {
    flex-direction: column-reverse;
    align-items: center;
  }

  .about-header-content {
    width: 100%;
    height: auto;
    overflow-y: hidden;
    padding: 0;
  }
}


// Tablet Screens
@media only screen and (min-width: 481px) and (max-width: 768px) {
  .about-container {
    flex-direction: column-reverse;
    align-items: center;
  }

  .about-header-content {
    width: 100%;
    height: auto;
    overflow-y: hidden;
    padding: 0;
  }
}

